@import url('https://fonts.googleapis.com/css2?family=Oxygen:wght@300;400;700&family=Righteous&display=swap');

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    list-style: none;
    text-decoration: none;
    font-family: 'Oxygen', sans-serif;

}

h4 {
    font-family: 'Righteous', cursive;
    font-size: 3vw;
    margin-top: 1vw;
}

.container {
    transition: opacity 1s, transform 1s;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    background-color: gray;

    &.hidden {
        opacity: 0;
        transform: translateY(50%);
    }


    .display {
        width: 45%;
        height: 100%;

        img {
            height: 100%;
            object-fit: cover;
            border: 1px solid white;
        }
    }

    .thumbnails {
        display: flex;
        justify-content: space-evenly;
        flex-direction: column;
        height: 100%;

        img {
            width: 100%;
            height: 25%;
            object-fit: cover;
            cursor: pointer;
            background: white;
            padding: 1vw 1vw 2vw 1vw;
        }
    }

    

    .description {
        width: 35%;
        margin-left: auto;
        margin-right: 20px;

        h4 {
            font-size: 3vw;
        }
    }
}


@media screen and (max-width: 645px) {
    .container{
        flex-direction: column;
        align-items: center;
        .thumbnails {
            flex-direction: row;
            margin: 0;
            gap: 1vw;
            position: absolute;
        }

        .display{
            width: 100%;
        }
    }
}

@media screen and (max-width: 950px) {
    .container{
        justify-content: space-between;
        .thumbnails {
            margin-right: 17vw;
        }
        .description {
            display: none;
        }
    }
}